@import "../../style/variables.scss";

.char {
	&__content {
		margin-top: 50px;
		display: grid;
		grid-template-columns: 650px 425px;
		column-gap: 25px;
		align-items: start;
	}
	&__grid {
		display: grid;
		grid-template-columns: repeat(3, 200px);
		column-gap: 25px;
		row-gap: 30px;
	}
	&__item {
		width: 200px;
		height: 318px;
		background-color: $dark;
		box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
		padding: 15px;
		cursor: pointer;
		transition: 0.3s transform;
		img {
			width: 200px;
			height: 200px;
			object-fit: cover;
			transform: translate(-15px, -15px);
		}
		&_selected {
			box-shadow: 0 5px 20px $main-color;
			transform: translateY(-8px);
		}
	}
	&__name {
		font-weight: bold;
		font-size: 22px;
		line-height: 29px;
		text-transform: uppercase;
		color: #fff;
	}
}

@media (max-width: $smallLaptop) {
	main {
		max-width: 950px;
	}
	.char {
		&__content {
			grid-template-columns: 500px 425px;
		}
		&__grid {
			grid-template-columns: repeat(auto-fill, 200px);
			justify-content: center;
		}
	}
}
@media (max-width: $tablet) {
	main {
		max-width: 750px;
	}
	.char {
		&__content {
			grid-template-columns: 1fr;
			justify-content: center;
		}
	}
}

@media (max-width: $Bigmobile) {
	main {
		max-width: 560px;
	}
	.char {
	}
}
@media (max-width: $mobile) {
	main {
		max-width: 450px;
	}
}

@media (max-width: $mobileSmall) {
	main {
		max-width: 315px;
	}
	.char {
		&__grid {
			grid-template-columns: repeat(auto-fill, 210px);
		}
	}
}
